.catalog-promo {
    position: relative;
    min-height: 261px;
    background-image: url("../img/catalog-promo.jpg");
    background-size: cover;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    background-color: $basic-brown;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-bottom: 80px;

    @media (min-resolution:2dppx) {
        background-image: url("../img/catalog-promo@2x.jpg");
    }

    @media (max-width: $tablet-width) {
        background-image: url("../img/catalog-promo-tablet.jpg");
        margin-bottom: 50px;

        @media (min-resolution:2dppx) {
            background-image: url("../img/catalog-promo-tablet@2x.jpg");
        }
    }

    @media (max-width: $mobile-width) {
        background-image: url("../img/catalog-promo-mobile.jpg");
        margin-bottom: 40px;

        @media (min-resolution:2dppx) {
            background-image: url("../img/catalog-promo-mobile@2x.jpg");
        }
    }
}

.catalog-promo::before {
    position: absolute;
    width: 100%;
    min-height: 261px;
    top: 0;
    content: "";
    display: block;
    background: rgba(86, 64, 52, .7);
}

.catalog-promo__info {
    position: relative;
    z-index: 3;
    color: $basic-white;

    @media (max-width: $mobile-width) {
        padding: 0 36px;
    }  
}

.catalog-promo__info h2 {
    @include title;
    text-transform: uppercase;
}

.catalog-promo__info p {
    font-family: "CormorantGaramond";
    font-size: 22px;
    font-style: italic;
    font-weight: 500;
    line-height: 29px;
    text-transform: none;

    @media (max-width: $mobile-width) {
        line-height: 26px;
    }    
}