@font-face {
  font-family: "CormorantGaramond";
  font-display: swap;
  src: url("../fonts/CormorantGaramond-Regular.woff2"), url("../fonts/CormorantGaramond-Regular.woff");
  font-weight: 400;
}
@font-face {
  font-family: "CormorantGaramond";
  font-display: swap;
  src: url("../fonts/CormorantGaramond-Medium.woff2"), url("../fonts/CormorantGaramond-Medium.woff");
  font-weight: 500;
}
@font-face {
  font-family: "CormorantGaramond";
  font-display: swap;
  src: url("../fonts/CormorantGaramond-SemiBold.woff2"), url("../fonts/CormorantGaramond-SemiBold.woff");
  font-weight: 600;
}
@font-face {
  font-family: "CormorantGaramond";
  font-display: swap;
  src: url("../fonts/CormorantGaramond-Bold.woff2"), url("../fonts/CormorantGaramond-Bold.woff");
  font-weight: 700;
}
@font-face {
  font-family: "Poppins";
  font-display: swap;
  src: url("../fonts/Poppins-Regular.woff2"), url("../fonts/Poppins-Regular.woff");
  font-weight: 400;
}
@font-face {
  font-family: "Poppins";
  font-display: swap;
  src: url("../fonts/Poppins-Medium.woff2"), url("../fonts/Poppins-Medium.woff");
  font-weight: 500;
}
@font-face {
  font-family: "Poppins";
  font-display: swap;
  src: url("../fonts/Poppins-Bold.woff2"), url("../fonts/Poppins-Bold.woff");
  font-weight: 700;
}
html {
  overflow-x: hidden;
  box-sizing: border-box;
}

.page {
  margin: 0 auto;
  padding: 0;
  min-width: 320px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-family: "Poppins", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: #1d1613;
  background-color: #ffffff;
  overflow: hidden;
}

.page--disabled {
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.container {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 98px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.page-main {
  flex-grow: 1;
}

.page-footer {
  flex-shrink: 0;
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 68px;
  background-color: #a87b62;
  color: #ffffff;
  padding: 10px;
  font-family: "Poppins", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  border: 0;
  text-align: center;
  box-sizing: border-box;
  cursor: pointer;
}
.button:hover, .button:focus {
  background-color: rgba(86, 64, 52, 0.7);
}

.login {
  display: none;
  position: fixed;
  z-index: 10;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
}

.login__wrapper {
  position: absolute;
  top: 4%;
  left: 50%;
  transform: translate(-50%, 0);
  width: 430px;
  background-color: #f0eae4;
  box-shadow: 0px 5px 32px rgba(226, 221, 215, 0.24);
  background-image: url("../img/login-bg.jpg");
  background-position: 50% 0;
  background-repeat: no-repeat;
  background-size: auto 138px;
  padding: 138px 45px 55px;
  box-sizing: border-box;
  text-align: center;
}
@media (min-resolution: 2dppx) {
  .login__wrapper {
    background-image: url("../img/login-bg@2x.jpg");
  }
}
@media (max-width: 1023px) {
  .login__wrapper {
    top: 80px;
    width: 442px;
    padding: 138px 51px 55px;
    background-image: url("../img/login-bg-tablet.jpg");
  }
}
@media (max-width: 1023px) and (min-resolution: 2dppx) {
  .login__wrapper {
    background-image: url("../img/login-bg-tablet@2x.jpg");
  }
}
@media (max-width: 767px) {
  .login__wrapper {
    top: 50px;
    width: 270px;
    padding: 84px 15px 35px;
    background-image: url("../img/login-bg-mobile.jpg");
    background-size: auto 84px;
  }
}
@media (max-width: 767px) and (min-resolution: 2dppx) {
  .login__wrapper {
    background-image: url("../img/login-bg-mobile@2x.jpg");
  }
}

.login-form {
  position: relative;
  display: flex;
  flex-direction: column;
}

.login-form h2 {
  font-family: "CormorantGaramond", "Georgia", serif;
  font-size: 34px;
  font-style: normal;
  font-weight: 700;
  line-height: 44px;
  letter-spacing: 0.03em;
  margin: 0;
  margin-top: 50px;
  margin-bottom: 33px;
}
@media (max-width: 767px) {
  .login-form h2 {
    font-size: 24px;
    line-height: 31px;
    margin-top: 40px;
    margin-bottom: 30px;
  }
}

.login-form input {
  font-family: "Poppins", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 21px;
  color: #9B9997;
  width: 100%;
  padding: 0 0 8px;
  border: none;
  border-bottom: 1px solid #1d1613;
  background-color: inherit;
}
.login-form input:hover, .login-form input:focus {
  opacity: 0.5;
  outline: none;
}
.login-form input::placeholder {
  opacity: 0.5;
}

.login-form__field:not(:last-child) {
  margin-bottom: 34px;
}
@media (max-width: 767px) {
  .login-form__field:not(:last-child) {
    margin-bottom: 25px;
  }
}

.login-form__field a {
  display: block;
  font-family: "Poppins", "Arial", sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  color: #9B9997;
  text-align: right;
  margin-top: 5px;
  text-decoration: none;
}
@media (max-width: 767px) {
  .login-form__field a {
    margin-top: 5px;
  }
}
.login-form__field a:hover, .login-form__field a:focus {
  opacity: 0.5;
}

.login-form button {
  margin-top: 10px;
  margin-bottom: 16px;
}

.login-form p {
  font-size: 14px;
  font-weight: 500;
  line-height: 21px;
  color: #9B9997;
  margin: 0;
}

.login-form p a {
  color: #1d1613;
  margin-left: 5px;
  text-decoration: none;
}
.login-form p a:hover, .login-form p a:focus {
  opacity: 0.5;
}

.login__close-button {
  width: 14px;
  height: 14px;
  background-image: url("../img/icon-close.svg");
  background-repeat: no-repeat;
  background-size: 14px 14px;
  background-color: transparent;
  border: none;
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;
}
.login__close-button:hover {
  outline: 2px solid #A87B62;
  outline-offset: 5px;
}

.login--opened {
  display: block;
}

.overlay--show {
  display: block;
}

.overlay {
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 5;
  width: 100%;
  height: 100%;
  background-color: rgba(86, 64, 52, 0.7);
}

.page-header {
  position: relative;
  padding: 30px 0 0;
  color: #1d1613;
}
@media (max-width: 767px) {
  .page-header {
    padding: 20px 0 0;
  }
}

.page-header::before {
  content: "";
  width: 100%;
  height: 1px;
  background-color: #F6F5F4;
  position: absolute;
  top: 115px;
  left: 0;
}
@media (max-width: 1023px) {
  .page-header::before {
    display: none;
  }
}

.page-header a {
  font-family: "Poppins";
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  text-decoration: none;
  color: inherit;
}

.page-header ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.page-header__top-nav {
  display: flex;
  justify-content: space-between;
  padding-bottom: 19px;
  flex-wrap: wrap;
}
@media (max-width: 1023px) {
  .page-header__top-nav {
    align-items: center;
  }
}

.page-header__logo {
  margin: 0 auto;
  flex-shrink: 0;
  display: block;
  background-size: cover;
  background-repeat: no-repeat;
}
.page-header__logo:hover, .page-header__logo:focus {
  opacity: 0.6;
}

.page-header__logo svg {
  fill: #A87B62;
}
@media (max-width: 767px) {
  .page-header__logo svg {
    width: 52px;
    height: 52px;
  }
}

.page-header__search {
  display: flex;
  align-items: center;
  position: relative;
}
@media (max-width: 1023px) {
  .page-header__search {
    display: none;
  }
}

.page-header__login {
  margin-right: 35px;
}
@media (max-width: 1023px) {
  .page-header__login {
    display: none;
  }
}

.page-header__user-menu {
  padding-top: 17px;
}
@media (max-width: 1023px) {
  .page-header__user-menu {
    padding-top: 0;
  }
}

.page-header__main-nav {
  display: flex;
  align-items: center;
  min-height: 61px;
  justify-content: center;
}

.page-header__cart svg {
  display: none;
}
@media (max-width: 1023px) {
  .page-header__cart svg {
    display: block;
    fill: none;
    stroke: #1d1613;
  }
}

@media (max-width: 1023px) {
  .page-header__cart span {
    display: none;
  }
}

.page-header__toggle {
  display: none;
}
@media (max-width: 1023px) {
  .page-header__toggle {
    display: block;
    border: none;
    background-image: url("../img/icon-burger.svg");
    background-color: white;
    width: 25px;
    height: 18px;
    cursor: pointer;
  }
}

.page-header--opened {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 99;
  animation: swing-in-top-fwd 1s cubic-bezier(0.17, 0.88, 0.32, 1.27) both;
}

@media (max-width: 1023px) {
  .page-header--no-js,
.page-header--opened {
    background-color: #A87B62;
  }
}

@media (max-width: 1023px) {
  .page-header--opened .page-header__toggle {
    background-color: #A87B62;
    background-image: url("../img/icon-burger-white.svg");
  }
}

.page-header--opened .page-header__cart svg {
  stroke: #ffffff;
}
.page-header--opened .page-header__cart svg:hover {
  stroke: #9B9997;
}

.page-header--opened .page-header__logo svg {
  fill: #ffffff;
}

.page-header--no-js .page-header__search,
.page-header--opened .page-header__search {
  margin-top: 39px;
}

.page-header--opened .page-header__search {
  display: block;
  width: 100%;
  z-index: 55;
  order: 2;
  margin-bottom: 36px;
}
@media (max-width: 767px) {
  .page-header--opened .page-header__search {
    margin-bottom: 10px;
    margin-top: 25px;
  }
}
.page-header--opened .page-header__search input:hover, .page-header--opened .page-header__search input:active {
  opacity: 0.75;
  outline: 1px solid #ffffff;
}

.page-header--opened input {
  display: flex;
  color: #ffffff;
  background-color: rgba(255, 255, 255, 0.15);
  border-radius: 21px;
  width: 100%;
}

.page-header--opened input::placeholder {
  font-size: 0;
}

.page-header--opened .page-header__search svg {
  left: 12px;
  fill: #ffffff;
  width: 16px;
  height: 16px;
}

.page-header--opened .main-nav,
.page-header--opened .about-list,
.page-header--opened .page-header__login {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.page-header--opened .main-nav__list {
  flex-direction: column;
  margin-bottom: 35px;
}
@media (max-width: 767px) {
  .page-header--opened .main-nav__list {
    margin-bottom: 15px;
  }
}

.page-header--opened .main-nav__list a,
.page-header--opened .main-nav__login a {
  font-size: 24px;
  line-height: 36px;
  color: #ffffff;
  padding-left: 0;
}
.page-header--opened .main-nav__list a:hover, .page-header--opened .main-nav__list a:active,
.page-header--opened .main-nav__login a:hover,
.page-header--opened .main-nav__login a:active {
  color: rgba(86, 64, 52, 0.7);
}

.page-header--opened .about-list a,
.page-header--opened .main-nav__login a {
  font-size: 18px;
  line-height: 24px;
  display: block;
  padding: 10px;
  padding-left: 0;
  opacity: 0.75;
}

.page-header--opened .main-nav__login a {
  opacity: 1;
}

.page-header--opened .page-header__login {
  display: none;
}

.page-no-scroll {
  overflow: hidden;
}

.search input {
  font-family: "Poppins";
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 21px;
  border: none;
  padding: 10px 12px 10px 30px;
}
.search input:hover, .search input:focus {
  color: #A87B62;
}

.search svg {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  fill: #000000;
  width: 14px;
  height: 14px;
}

@media (max-width: 1023px) {
  .main-nav {
    display: none;
  }
}

.main-nav__list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.main-nav__link {
  display: block;
  padding: 10px;
  color: inherit;
}

.main-nav__item {
  margin-right: 35px;
}

.main-nav__about-list {
  display: none;
}

.promo {
  position: relative;
  min-height: 463px;
  background-image: url("../img/promo-bg.jpg");
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-color: #A87B62;
  display: flex;
  align-items: center;
}
@media (min-resolution: 2dppx) {
  .promo {
    background-image: url("../img/promo-bg@2x.jpg");
  }
}
@media (max-width: 1023px) {
  .promo {
    background-image: url("../img/promo-bg-tablet.jpg");
  }
}
@media (max-width: 1023px) and (min-resolution: 2dppx) {
  .promo {
    background-image: url("../img/promo-bg-tablet@2x.jpg");
  }
}
@media (max-width: 767px) {
  .promo {
    background-image: url("../img/promo-bg-mobile.jpg");
  }
}
@media (max-width: 767px) and (min-resolution: 2dppx) {
  .promo {
    background-image: url("../img/promo-bg-mobile@2x.jpg");
  }
}

.promo::before {
  position: absolute;
  width: 100%;
  min-height: 463px;
  top: 0;
  content: "";
  display: block;
  background: rgba(86, 64, 52, 0.7);
}

.promo__info {
  position: relative;
  z-index: 3;
  color: #ffffff;
}

.promo__info p {
  font-family: "CormorantGaramond", "Georgia", serif;
  font-size: 45px;
  font-style: normal;
  font-weight: 700;
  line-height: 58px;
  letter-spacing: 0.03em;
  margin: 0;
  text-transform: uppercase;
