.pagination-catalog {
    border-top: 1px solid $border-line;
    margin-bottom: 72px;
}

.pagination-catalog__wrapper {
    display: grid;
    grid-template-columns: 250px 1fr;
    gap: 4.2%;
    padding-top: 35px;

    @media (max-width: $tablet-width) {
        grid-template-columns: 1fr;
    }
}

.pagination-catalog__inner {
    grid-column: 2/3;
    display: flex;
    justify-content: space-between;

    @media (max-width: $tablet-width) {
        grid-column: 1/3;
    }

    @media (max-width: $mobile-width) {
        justify-content: center;
    }
}

.pagination-catalog__list {
    @include list-reset;
    display: flex;
    flex-wrap: wrap;
}

.pagination-catalog__button {
    min-width: 90px;
    padding: 2px;
    vertical-align: middle;
    font-family: inherit;
    font-weight: 500;
    font-size: 16px;
    line-height: 21px;
    background-color: transparent;
    border: 0;
    cursor: pointer;

    @media (max-width: $mobile-width) {
        display: none;
    } 

    &:hover,
    &:focus {
        color: $basic-brown;
    }
}

.pagination-catalog__button--prev {
    text-align: left;
}

.pagination-catalog__button--next {
    text-align: right;
}

.pagination-catalog__item {
    padding: 2px;
    font-family: inherit;
    font-weight: 500;
    font-size: 16px;
    line-height: 21px;
    color: $basic-grey;
    background-color: transparent;
    cursor: pointer;

    & a {
        color: inherit;
        text-decoration: none;

        &:hover,
        &:focus {
            color: $basic-brown;
        }
    }
}

.pagination-catalog__item:not(:last-child) {
    margin-right: 10px;
}

.pagination-catalog__item--current {
    color: $dark-grey;
}