.filter__toggle-button {
    display: none;
    text-decoration: none;

    @media (max-width: $tablet-width) {
        display: flex;
        margin-bottom: 50px;
    }
}

.filter__wrapper {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    width: 100%;
    margin-bottom: 40px;

    @media (max-width: $tablet-width) {
        display: none;
        padding-bottom: 40px;
    }
}

.filter__list {
    @include list-reset;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    margin: 0 0 32px;
}

.filter__item {
    padding: 22px;
    border: 1px solid $basic-brown;

    &:not(:first-child) {
        border-top: none;
    }
}

.filter fieldset {
    margin: 0;
    padding: 0;
    border: 0;
}

.filter__toggle {
    margin: 0;
    padding: 0;
    display: block;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;

    & span {
        line-height: 24px;
        font-weight: 500;
    }

    & button {
        width: 15px;
        height: 8px;
        padding: 0;
        border: 0;
        background-color: transparent;
        color: $basic-brown;
        background-image: url('../img/arrow-down.svg');
        background-repeat: no-repeat;
        background-size: 15px 8px;
    }
}

.filter__toggle--opened {
    & button {
        width: 15px;
        height: 8px;
        padding: 0;
        border: 0;
        background-color: transparent;
        color: $basic-brown;
        background-image: url('../img/arrow-up.svg');
        background-repeat: no-repeat;
        background-size: 15px 8px;
    }
}

.filter__item-content ul {
    @include list-reset;
    padding: 20px 10px 4px;
}

.filter__item-content li {
    padding-left: 28px;
    font-size: 14px;
    line-height: 18px;
    font-weight: 500;

    &:not(:last-child) {
        margin-bottom: 11px;
    }
}

.filter__item-content input {
    display: none;
}

.filter__item-content input+label {
    position: relative;
    vertical-align: middle;
    cursor: pointer;
}

.filter__item-content input+label::before {
    position: absolute;
    content: "";
    width: 15px;
    height: 15px;
    left: -27px;
    top: 3px;
    box-sizing: border-box;
    border: 1px solid $basic-brown;
    background-color: $basic-white;
}

.filter__item-content input:checked+label::before {
    background-color: $basic-brown;
}

.filter__price {
    padding-top: 18px;
}

.filter__price-wrapper {
    position: relative;
    width: 100%;
    height: 35px;
}

.filter__price-scale {
    position: absolute;
    width: 100%;
    height: 1px;
    top: 8px;
    background-color: $light-brown;
}

.filter__price-bar {
    position: absolute;
    height: 1px;
    width: 45%;
    top: 0;
    left: 29.5%;
    background-color: $basic-brown;
}

.filter__price-toggle {
    position: absolute;
    width: 15px;
    height: 15px;
    cursor: pointer;
    background-color: $basic-brown;
    border-radius: 50%;

    &--min {
        left: 23%;
    }

    &--max {
        left: 74.5%;
    }

    & span {
        position: absolute;
        top: 20px;
        left: -7px;
        font-size: 14px;
        font-weight: 500;
        line-height: 18px;
    }
}

.filter__submit {
    margin-bottom: 25px;
    width: 100%;

    &:focus,
    &:hover {
        background-color: $basic-white;
        border: 1px solid $basic-brown;
        color: $dark-grey;
        outline: 3px solid $basic-brown;
    }
}

.filter__reset {
    background-color: $basic-white;
    border: 1px solid $basic-brown;
    color: $dark-grey;
    width: 100%;

    &:focus,
    &:hover {
        background-color: $basic-white;
        outline: 3px solid $basic-brown;
    }
}

.filter__button-close {
    display: none;
}

.filter__wrapper--open .filter__button-close {
    display: block;
    position: absolute;
    top: -15px;
    right: -14px;
    width: 32px;
    height: 32px;
    background-image: url('../img/icon-button-close.svg');
    background-repeat: no-repeat;
    background-size: 32px;
    background-color: transparent;
    border: 0;
    z-index: 2;
}

.filter__wrapper,
.filter__wrapper--close {
    @media (max-width: $tablet-width) {
        display: none;
    }
}

.filter__wrapper--open {
    @media (max-width: $tablet-width) {
        position: absolute;
        top: 4%;
        left: 50%;
        transform: translate(-50%, 0);
        display: block;
        max-width: 425px;
        min-height: fit-content;
        background-color: $basic-white;
        z-index: 5;
        box-shadow: 0px 5px 32px rgba(219, 213, 208, 0.24);
    }

    @media (max-width: $mobile-width) {
        max-width: 270px;
    }

    & .button {
        width: 85%;
        margin-left: auto;
        margin-right: auto;
    }

    & .filter__item {
        border: none;
        border-bottom: 1px solid $basic-brown;
    }
}