.page-header {
    position: relative;
    padding: 30px 0 0;
    color: $dark-grey;

    @media (max-width: $mobile-width) {
        padding: 20px 0 0;
    }
}

.page-header::before {
    content: "";
    width: 100%;
    height: 1px;
    background-color: $border-line;
    position: absolute;
    top: 115px;
    left: 0;

    @media (max-width: $tablet-width) {
        display: none;
    }
}

.page-header a {
    font-family: "Poppins";
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    text-decoration: none;
    color: inherit;
}

.page-header ul {
    @include list-reset;
}

.page-header__top-nav {
    display: flex;
    justify-content: space-between;
    padding-bottom: 19px;
    flex-wrap: wrap;

    @media (max-width: $tablet-width) {
        align-items: center;
    }
}

.page-header__logo {
    margin: 0 auto;
    flex-shrink: 0;
    display: block;
    background-size: cover;
    background-repeat: no-repeat;

    &:hover,
    &:focus {
        opacity: 0.6;
    }
}

.page-header__logo svg {
    fill: $basic-brown;

    @media (max-width: $mobile-width) {
        width: 52px;
        height: 52px;
    }
}

.page-header__search {
    display: flex;
    align-items: center;
    position: relative;

    @media (max-width: $tablet-width) {
        display: none;
    }
}

.page-header__login {
    margin-right: 35px;

    @media (max-width: $tablet-width) {
        display: none;
    }
}

.page-header__user-menu {
    padding-top: 17px;

    @media (max-width: $tablet-width) {
        padding-top: 0;
    }
}

.page-header__main-nav {
    display: flex;
    align-items: center;
    min-height: 61px;
    justify-content: center;
}

.page-header__cart svg {
    display: none;

    @media (max-width: $tablet-width) {
        display: block;
        fill: none;
        stroke: $dark-grey;
    }
}

.page-header__cart span {
    @media (max-width: $tablet-width) {
        display: none;
    }
}

.page-header__toggle {
    display: none;

    @media (max-width: $tablet-width) {
        display: block;
        border: none;
        background-image: url("../img/icon-burger.svg");
        background-color: white;
        width: 25px;
        height: 18px;
        cursor: pointer;
    }
}

.page-header--opened {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 99;
    animation: swing-in-top-fwd 1s cubic-bezier(.17, .88, .32, 1.27) both;
}

.page-header--no-js,
.page-header--opened {
    @media (max-width: $tablet-width) {
        background-color: $basic-brown;
    }
}

.page-header--opened .page-header__toggle {
    @media (max-width: $tablet-width) {
        background-color: $basic-brown;
        background-image: url("../img/icon-burger-white.svg");
    }
}

.page-header--opened .page-header__cart svg {
    stroke: $basic-white;

    &:hover {
        stroke: $basic-grey;
    }
}

.page-header--opened .page-header__logo svg {
    fill: $basic-white;
}

.page-header--no-js .page-header__search,
.page-header--opened .page-header__search {
    margin-top: 39px;
}

.page-header--opened .page-header__search {
    display: block;
    width: 100%;
    z-index: 55;
    order: 2;
    margin-bottom: 36px;

    @media (max-width: $mobile-width) {
        margin-bottom: 10px;
        margin-top: 25px;
    }

    & input:hover,
    & input:active {
        opacity: 0.75;
        outline: 1px solid $basic-white;
    }
}

.page-header--opened input {
    display: flex;
    color: $basic-white;
    background-color: rgba(255, 255, 255, .15);
    border-radius: 21px;
    width: 100%;
}

.page-header--opened input::placeholder {
    font-size: 0;
}

.page-header--opened .page-header__search svg {
    left: 12px;
    fill: $basic-white;
    width: 16px;
    height: 16px;
}

.page-header--opened .main-nav,
.page-header--opened .about-list,
.page-header--opened .page-header__login {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.page-header--opened .main-nav__list {
    flex-direction: column;
    margin-bottom: 35px;

    @media (max-width: $mobile-width) {
        margin-bottom: 15px;
    }
}

.page-header--opened .main-nav__list a,
.page-header--opened .main-nav__login a  {
    font-size: 24px;
    line-height: 36px;
    color: $basic-white;
    padding-left: 0;

    &:hover,
    &:active {
        color: $bg-promo;
    }
}

.page-header--opened .about-list a,
.page-header--opened .main-nav__login a  {
    font-size: 18px;
    line-height: 24px;
    display: block;
    padding: 10px;
    padding-left: 0;
    opacity: 0.75;
}

.page-header--opened .main-nav__login a {
    opacity: 1;
}

.page-header--opened .page-header__login {
    display: none;
}

.page-no-scroll {
	overflow: hidden;
}
