.main-nav {    
    @media (max-width: $tablet-width) {
        display: none;
    }
}

.main-nav__list {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.main-nav__link {
    display: block;
    padding: 10px;
    color: inherit;
}

.main-nav__item {
    margin-right: 35px;
}

.main-nav__about-list {
    display: none;
}
