.gallery {
    text-align: center;
    margin-bottom: 132px;

    @media (max-width: $tablet-width) {
        margin-bottom: 80px;
    }

    @media (max-width: $mobile-width) {
        margin-bottom: 70px;
    }
}

.gallery p {
    @include title;
    margin-bottom: 35px;
}

.gallery span {
    font-size: 42px;
}

.gallery__link {
    display: inline-block;
    border-bottom: 1px solid $basic-brown;
    margin-bottom: 60px;
    font-weight: 500;
    line-height: 21px;
    text-align: center;
    color: $dark-grey;
    text-decoration: none;
    outline: 0;

    @media (max-width: $mobile-width) {
        margin-bottom: 40px;
    }
}

.gallery__list {
    @include list-reset;
    display: grid;
    grid-template-columns: 1fr 1.74fr 1fr 1fr;
    grid-template-rows: 230px 150px;
    grid-gap: 34px 27px;

    @media (max-width: $tablet-width) {
        grid-template-rows: 133px 87px;
        grid-gap: 20px 16px;
    }

    @media (max-width: $mobile-width) {
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 133px 87px 87px;
        grid-gap: 27px 24px;
    }
}

.gallery__item {
    width: 100%;
    height: auto
}

.gallery__item img {
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.gallery__item--big {
    grid-row: 1 / 3;
    grid-column: 2 / 3;

    // @media (max-width: $mobile-width) {
    // max-width: 230px;
    // }
}

.gallery__item--mobile-none {
    @media (max-width: $mobile-width) {
        display: none;
    }
}