.new-goods {
    padding-top: 130px;
    text-align: center;
    margin-bottom: 110px;

    @media (max-width: $tablet-width) {
        padding-top: 80px;
        margin-bottom: 80px;
    }

    @media (max-width: $mobile-width) {
        padding-top: 70px;
        margin-bottom: 70px;
    }
}

.new-goods h2 {
    @include title;
    color: $dark-grey;
    margin-bottom: 13px;

    @media (max-width: $mobile-width) {
        margin-bottom: 30px;
    }
}

.new-goods__buttons {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 13px;
    position: relative;

    @media (max-width: $mobile-width) {
        display: none;
    }
}

.new-goods__button {
    padding: 0;
    border: none;
    cursor: pointer;
    position: absolute;
    display: block;
    background-color: transparent;
    top: 24px;
    width: 29px;
    height: 50px;
    color: transparent;
    background-repeat: no-repeat;
}

.new-goods__button--prev {
    background-image: url("../img/arrow-prev.svg");
    left: 0;
}

.new-goods__button--next {
    background-image: url("../img/arrow-next.svg");
    right: 0;
}

.new-goods a {
    text-decoration: none;
    color: $dark-grey;
}

.new-goods__link {
    display: inline-block;
    border-bottom: 1px solid $basic-brown;
    margin-bottom: 60px;
    font-weight: 500;
    line-height: 21px;
    text-align: center;
    color: $dark-grey;
    text-decoration: none;
    outline: 0;

    @media (max-width: $mobile-width) {
        margin-bottom: 40px;
    }
}

.new-goods__list {
    @include list-reset;
    column-gap: 2.5%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 30px;
    transform: translate3d(0px, 0px, 0px);
    overflow: hidden;

    @media (max-width: $mobile-width) {
        margin-bottom: 40px;
    }
}

.new-goods__wrapper {
margin-bottom: 60px;
}

.new-goods__item {
    width: 270px;
    height: 100%;
    margin: 0 auto;
    margin-bottom: 20px;
    margin-right: 30px;

    @media (max-width: $tablet-width) {
        width: 324px;
    }

    @media (max-width: $mobile-width) {
        width: 130px;
    }

    &:hover,
    &:focus {
        transform: scale(1.01);
        filter: drop-shadow(4px 4px 10px rgba(168, 122, 98, .24));
    }
}

.new-goods__item h3 {
    font-size: 16px;
    font-weight: 500;
    line-height: 21px;
    text-align: center;
    margin: 0;
    margin-bottom: 15px;
}

.new-goods__item p {
    font-size: 14px;
    font-weight: 700;
    line-height: 18px;
    text-align: center;
    margin: 0;
}

.new-goods__item img {
    width: 100%;
    height: 100%;
    margin-bottom: 25px;
}